//  上传身份证页面
 */

<template>
  <div>
    <van-uploader v-model="fileListPeople" multiple :max-count="1" />
    <h3>上传身份证人像面</h3>

    <van-uploader v-model="fileListCountry" multiple :max-count="1" />
    <h3>上传身份证国徽面</h3>
    <hr />

    <van-form class="form" validate-first @failed="onFailed" @submit="submit">
      <van-field
        v-model="username"
        name="姓名"
        label="姓名"
        placeholder="姓名"
        left-icon="contact"
        :rules="[{ required: true, message: '请填写姓名' }]"
      />
      <van-field
        v-model="usefulTime"
        name="有效期时间"
        label="有效期时间"
        placeholder="有效期时间"
        left-icon="underway-o"
        :rules="[{ required: true, message: '请填写有效期时间' }]"
      />
      <van-field
        v-model="idCard"
        name="身份证号"
        label="身份证号"
        placeholder="请输入身份证"
        :rules="[{ validator, message: '身份证号码格式错误！' }]"
        left-icon="idcard"
      >
      </van-field>

      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          >提交</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import isIdcard from "./JS/index";
import { Toast } from "vant";

export default {
  name: "",
  components: {},

  data() {
    return {
      fileListPeople: [],
      fileListCountry: [],
      idCard: "",
      username: "",
      usefulTime: "",
    };
  },

  created() {},
  mounted() {},
  methods: {
    // 表单成功的回调
    submit() {
      // Toast.success("正确");
      this.$router.push({
        path: "/errPages",
      });
    },
    // 表单失败的回调
    onFailed(errorInfo) {
      console.log("failed", errorInfo);
      Toast.fail(errorInfo.errors[0].message);
    },
    // 身份证验证
    validator(val) {
      return isIdcard(val);
    },
  },
  watch: {},
  computed: {},
};
</script>

<style lang='scss' scoped>
</style>
